.site-header {
    background-color: #20232a;
    color: #ffffff;
    position: fixed;
    z-index: 1;
    width: 100%;
    top: 0;
    left: 0;
}

.header-wrapper {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
}

.header-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.site-title {
    display: flex;
    flex-direction: column;
}

.author-title {
    font-size: 0.85rem;
}

.header-social {
    display: flex;
}

.header-social a {
    font-size: 1.2rem;
    padding: 1rem .5rem;
}

